"use client";

import React, { useState, useEffect, useRef, useCallback } from "react";
import { MathProblem } from "@/app/elements/MathProblem"; // Corrected path
// import { generateProblem, checkAnswer } from "../../utils/mathUtils"; // Temporarily commented out
// import FeedbackMessage from "@/app/components/FeedbackMessage"; // Corrected path - Temporarily commented out
import FeedbackSuccessAnimation from "@/app/components/FeedbackSuccessAnimation"; // Corrected path
// Removed unused Image import

const AdditionGame: React.FC = () => {
  const [num1, setNum1] = useState(0);
  const [num2, setNum2] = useState(0);
  const [userAnswer, setUserAnswer] = useState("");
  // const [feedback, setFeedback] = useState(""); // Removed as FeedbackMessage is commented out
  const [showFeedback, setShowFeedback] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const inputRef = useRef<HTMLInputElement | null>(null);
  const feedbackTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  const 문제를내다 = useCallback(() => {
    // const { num1: n1, num2: n2 } = generateProblem(10, 0); // Max sum of 10, min num 0 // Temporarily commented out
    // For now, using placeholder numbers:
    const n1 = Math.floor(Math.random() * 10) + 1;
    const n2 = Math.floor(Math.random() * 10) + 1;
    setNum1(n1);
    setNum2(n2);
    setUserAnswer("");
    // setFeedback(""); // Removed
    setShowFeedback(false);
    setIsCorrect(false);
    inputRef.current?.focus();
  }, []);

  useEffect(() => {
    문제를내다();
    return () => {
      if (feedbackTimeoutRef.current) {
        clearTimeout(feedbackTimeoutRef.current);
      }
    };
  }, [문제를내다]);

  const handleAnswerSubmit = () => {
    const answer = parseInt(userAnswer, 10);
    // const { correct, message } = checkAnswer(num1, num2, answer, "+"); // Temporarily commented out
    // For now, using placeholder logic:
    const expectedAnswer = num1 + num2;
    const correct = answer === expectedAnswer;
    // const message = correct ? "Correct!" : "Try again!"; // Removed
    setIsCorrect(correct);
    setShowFeedback(true);

    if (feedbackTimeoutRef.current) {
      clearTimeout(feedbackTimeoutRef.current);
    }

    feedbackTimeoutRef.current = setTimeout(() => {
      setShowFeedback(false);
      if (correct) {
        문제를내다();
      } else {
        setUserAnswer("");
        inputRef.current?.focus();
      }
    }, 2000);
  };

  const handleUserAnswerChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const value = event.target.value;
    // Allow only numbers and limit to 2 digits
    if (/^\d{0,2}$/.test(value)) {
      setUserAnswer(value);
    }
  };

  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === "Enter") {
      if (userAnswer !== "") {
        handleAnswerSubmit();
      } else {
        // setFeedback("Please enter an answer."); // Removed
        setIsCorrect(false);
        setShowFeedback(true);
        if (feedbackTimeoutRef.current) {
          clearTimeout(feedbackTimeoutRef.current);
        }
        feedbackTimeoutRef.current = setTimeout(() => {
          setShowFeedback(false);
        }, 1500);
      }
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-br from-indigo to-persian-indigo p-4 text-white relative overflow-hidden">
      {isCorrect && <FeedbackSuccessAnimation show={showFeedback} />}

      <div
        className={`transition-opacity duration-300 ease-in-out ${
          showFeedback ? "opacity-25 blur-sm" : "opacity-100"
        }`}
      >
        <h1 className="text-5xl md:text-7xl font-bold text-aureolin mb-12 text-center">
          Addition Game
        </h1>
        <MathProblem
          num1={num1}
          num2={num2}
          operator="+"
          userAnswer={userAnswer}
          onUserAnswerChange={handleUserAnswerChange}
          onKeyDown={handleKeyDown}
          isFeedbackShowing={showFeedback}
          inputRef={inputRef}
          num1VisualizerColor="bg-raspberry"
          num2VisualizerColor="bg-princeton-orange"
          inputAriaLabel="Enter your answer for the addition problem"
        />
        <button
          onClick={handleAnswerSubmit}
          disabled={userAnswer === "" || showFeedback}
          className="mt-12 px-10 py-4 bg-aureolin text-indigo-dark font-bold text-2xl rounded-lg shadow-lg hover:bg-yellow-400 transition-colors duration-200 disabled:bg-gray-400 disabled:cursor-not-allowed"
        >
          Check Answer
        </button>
      </div>

      {/* {showFeedback && (
        <FeedbackMessage
          message={feedback} // feedback state is removed
          isCorrect={isCorrect}
          className="absolute bottom-10 md:bottom-16"
        />
      )} */}
    </div>
  );
};

export default AdditionGame;
